.contacts-block {
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  text-transform: uppercase;
  color: $color-white;
  margin-bottom: 16px;

  &__adress {
    margin: 0;
    padding-bottom: 3px;

    &:last-child {
      padding-top: 10px;
    }
  }
  
  &__phone {
    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    text-decoration: none;
    color: $color-white;
  }
}
